@import url('./assets/iconfont/iconfont.css');
.slide-enter-active,
.slide-leave-active {
  transition: all 0.3s ease;
}
.slide-enter,
.slide-leave {
  transform: translateX(-100%);
}

.weui-tabbar {
  position: fixed;
  z-index: 50;
}
.weui-tabbar__item {
  text-decoration: none;
}



